import React from 'react';

import app from '../../EP';

export default class Popup extends React.Component {
  closePopup() {
    this.refs[this.props.role].className = this.props.role;
    // FIXME
    app.el['mask'].style.visibility = 'hidden';
  }

  render() {
    return (
      <div className={this.props.role} ref={this.props.role}>
        {this.props.children}
        <div className="close" onClick={this.closePopup.bind(this)}>&times;</div>
      </div>
    );
  }
};



