import React from 'react';
import app from '../../../EP';

import MenuButton from '../../controls/menu/button';
  
export default class ButtonGameRules extends MenuButton {
  handler() {
    app.showRules();
  }

  render() {
    return (
      <MenuButton
        hint="Как играть в пасьянс-косынку"
        role="btn4"
        text="Правила"
        handler={this.handler}
      />
    );
  }
}



