import actions from '../constants/actions';

export default {
  write: function(record) {
    return function(dispatch, getState) {
      var state = getState();
      var isWeak = true;
      if (state.records.length === 0) {
        isWeak = false;
        dispatch({
          index   : 0,
          record  : record,
          type    : actions.NEW_RECORD
        });
      } else {
        for (var i = 0; i < state.records.length; i++) {
          if (state.records[i].moves > record.moves) {
            dispatch({
              index   : i,
              record  : record,
              type    : actions.NEW_RECORD
            });
            isWeak = false;
            break;
          }
        }
      }

      if (isWeak) {
        dispatch({
          record  : record,
          type    : actions.WEAK_RECORD
        });
      }
    };
  }
};



