import React from 'react';
import app from '../../../EP';

import MenuButton from '../../controls/menu/button';
  
export default class ButtonGameRecords extends MenuButton {
  handler() {
    app.showRanks();
  }

  render() {
    return (
      <MenuButton
        hint="Авторизация, статистика, рекорды"
        role="btn5"
        text="Рекорды"
        handler={this.handler}
      />
    );
  }
}



