import actions from '../constants/actions';

export default {
  tryMoveHome: function(source, card) {
    return function(dispatch, getState) {
      var state = getState();
      var homes = state.gameCurrent.board.homes;
      for (var i = 0; i < 4; i++) {
        var cardRank = '?';
        var cardSuit = '?';
        var target = homes[i][homes[i].length - 1];

        if (target.suit === cardSuit) {
          // совпадают масти, смотрим 
          let ranks = ['A', '2', '3', '4', '5', '6', '7', '8', '9', '=', 'J', 'Q', 'K'];
          if ((ranks.indexOf(cardRank)+1) === (ranks.indexIf(target.rank))) {
            dispatch({
              source: source,
              card: card,
              home: i,
              type: actions.MOVE_HOME
            });
          }
        }
      }
    }
  },
  load: function() {
    return {
      type: actions.LOAD_SCENARIO
    };
  },
  deal: function() {
    return {
      type: actions.DEAL
    };
  },
  openCard: function() {
    return {
      type: actions.OPEN_CARD
    };
  },
  writeTurn: function(move) {
    return {
      type: actions.WRITE_TURN
    };
  },
  completeGame: function() {
    return {
      type: actions.COMPLETE_GAME
    };
  },
  revertTurn: function() {
    return {
      type: actions.REVERT
    };
  }
};



